import React from "react";
import { Paper, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import industry_background3 from "../img/industry_background3.png";
import { useTranslation } from "react-i18next";

const useStyles = makeStyles({
  content4: {
    display: "flex",
    justifyContent: "space-between",
    flexWrap: "wrap",
    gridArea: "r-line5 / c-line1 / r-line6 / c-line3",
    height: "100vh",
    backgroundColor: "#212121!important",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "top center",
  },
  half4: {
    height: "100vh",
    width: "50%",
    display: "flex",
    alignItems: "flex-end",
    justifyContent: "center",
  },
  line1: {
    display: "flex",
    flexDirection: "row",
    width: "100%",
  },
});

export default function Page4(props) {
  const { t, i18n } = useTranslation();
  const classes = useStyles();
  return (
    <Paper
      className={classes.content4}
      style={{ backgroundImage: `url(${industry_background3})` }}
    >
      <div
        className={classes.half4}
        style={{
          width: "100%",
          flexDirection: "column",
          alignItems: "flex-start",
          justifyContent: "space-around",
        }}
      >
        <div className={classes.line1}>
          <Typography
            style={{
              color: "#FFCF40",
              fontSize: "40px",
              letterSpacing: "0.15em",
              marginLeft: "10%",
              marginRight: "10%",
              textAlign: "left",
              width: "40%",
            }}
          >
            {t("PRZYGOTOWANIE PLC/HMI")}
          </Typography>
          <Typography
            style={{
              color: "#FFFFFF",
              fontSize: "21px",
              letterSpacing: "0.1em",
              marginRight: "5%",
              textAlign: "left",
              width: "60%",
            }}
          >
            {t(
              "Bazujc na zaoeniach projektowych oraz opisie funkcjonalnym przygotujemy kompleksowo program PLC sterujcy procesem oraz przygotujemy intuicyjn aplikacj panelu HMI."
            )}
          </Typography>
        </div>
        <div className={classes.line1} style={{ alignItems: "space-between" }}>
          <Typography
            style={{
              color: "#FFCF40",
              fontSize: "40px",
              letterSpacing: "0.15em",
              marginLeft: "10%",
              marginRight: "10%",
              textAlign: "left",
              width: "40%",
            }}
          >
            {t("INTEGRACJA")}
          </Typography>
          <Typography
            style={{
              color: "#FFFFFF",
              fontSize: "21px",
              letterSpacing: "0.1em",
              marginRight: "5%",
              textAlign: "left",
              width: "60%",
            }}
          >
            {t(
              "Nasi dowiadczeni programici zajm si kompleksowo integracj oraz uruchomieniem dostarczonego systemu."
            )}
          </Typography>
        </div>
        <div className={classes.line1}>
          <Typography
            style={{
              color: "#FFCF40",
              fontSize: "40px",
              letterSpacing: "0.15em",
              marginLeft: "10%",
              marginRight: "10%",
              textAlign: "left",
              width: "40%",
            }}
          >
            {t("SZKOLENIE")}
          </Typography>
          <Typography
            style={{
              color: "#FFFFFF",
              fontSize: "21px",
              letterSpacing: "0.1em",
              marginRight: "5%",
              width: "60%",
              textAlign: "left",
            }}
          >
            {t(
              "Po uruchomieniu systemu zadbamy o to, aby obsuga bya przygotowana na prac z nim oraz wiedziaa jakie czynnoci naley przeprowadza okresowo, aby zapewni jego bezawaryjn prac."
            )}
          </Typography>
        </div>
        <div className={classes.line1}>
          <Typography
            style={{
              color: "#FFCF40",
              fontSize: "40px",
              letterSpacing: "0.15em",
              marginLeft: "10%",
              marginRight: "10%",
              textAlign: "left",
              width: "40%",
            }}
          >
            {t("SERWIS")}
          </Typography>
          <Typography
            style={{
              color: "#FFFFFF",
              fontSize: "21px",
              letterSpacing: "0.1em",
              marginRight: "5%",
              width: "60%",
              textAlign: "left",
            }}
          >
            {t(
              "Nasz dzia serwisowy zadba o wsparcie obsugi w przypadku wystpienia takiej koniecznoci. Oferujemy obsug w jzyku polskim, angielskim, niemieckim oraz francuskim."
            )}
          </Typography>
        </div>
      </div>
    </Paper>
  );
}
